import React from 'react'
import { Link, useLocation } from 'react-router-dom'

import { RouterType } from '@/layouts/default'

import './index.scss'

const Menu: React.FC<{ routers: RouterType; isMobile: boolean }> = ({
  routers,
  isMobile
}) => {
  const { pathname } = useLocation()

  return (
    <header className="menu">
      <div className="container">
        <div className="logo"></div>
        {isMobile ? (
          <></>
        ) : (
          <div className="menu-links">
            <ul className="links">
              <li className="link-item">
                <Link
                  className={[
                    'link-element',
                    pathname === routers.home ? 'active' : ''
                  ].join(' ')}
                  to={routers.home}
                >
                  Home
                </Link>
              </li>
              <li className="link-item">
                <Link
                  className={[
                    'link-element',
                    pathname === routers.cloudHosting ? 'active' : ''
                  ].join(' ')}
                  to={routers.cloudHosting}
                >
                  Cloud Hosting
                </Link>
              </li>
              <li className="link-item">
                <Link
                  className={[
                    'link-element',
                    pathname === routers.webDevelopment ? 'active' : ''
                  ].join(' ')}
                  to={routers.webDevelopment}
                >
                  Web Development
                </Link>
              </li>
              <li className="link-item">
                <Link
                  className={[
                    'link-element',
                    pathname === routers.softwareService ? 'active' : ''
                  ].join(' ')}
                  to={routers.softwareService}
                >
                  Software Service
                </Link>
              </li>
              <li className="link-item">
                <Link
                  className={[
                    'link-element',
                    pathname === routers.dataCenterService ? 'active' : ''
                  ].join(' ')}
                  to={routers.dataCenterService}
                >
                  Data Center Service
                </Link>
              </li>
            </ul>
            <button
              className="contact-us"
              onClick={() => {
                document.getElementById('bottom')?.scrollIntoView()
              }}
            >
              Contact us
            </button>
          </div>
        )}
      </div>
    </header>
  )
}

export default React.memo(Menu)
