import React, { useState, useEffect, useMemo, useCallback } from 'react';
import { Switch, Route, Router, Redirect, useRouteMatch } from 'react-router';
import { useLocation } from 'react-router-dom';

import Menu from '@/components/menu';
import SubmitRequest from '@/components/submit-request';
import Footer from '@/components/footer';
import Banner from '@/components/banner';
import BackToTop from '@/components/back-to-top';

import Home from '@/pages/home';
import Cloud from '@/pages/cloud';
import Web from '@/pages/web';
import Software from '@/pages/software';
import Data from '@/pages/data';

import './App.scss';

export type RouterType = {
  home: string;
  cloudHosting: string;
  webDevelopment: string;
  softwareService: string;
  dataCenterService: string;
};

const userAgent = window.navigator.userAgent.toLowerCase();
const isMobile = !!(
  userAgent.match(/iphone/i) ||
  userAgent.match(/ipod/i) ||
  userAgent.match(/android/i) ||
  userAgent.match(/windows phone/i)
);

const DefaultLayout: React.FC = () => {
  const { path } = useRouteMatch();
  const routers = useMemo<RouterType>(() => {
    return {
      home: `${path}home`,
      cloudHosting: `${path}cloud`,
      webDevelopment: `${path}web`,
      softwareService: `${path}software`,
      dataCenterService: `${path}data`
    };
  }, [path]);

  const { pathname } = useLocation();
  const [message, setMessage] = useState('');

  const goSubmit = useCallback((message = '') => {
    document.getElementById('bottom')?.scrollIntoView();
    setMessage(message);
  }, []);

  useEffect(() => {
    setMessage('');
  }, [pathname]);

  return (
    <div className="default-container">
        <Menu routers={routers} isMobile={isMobile} />
        <Banner routers={routers} isMobile={isMobile} />
        <Switch>
          <Route path={routers.home} exact>
            <Home goSubmit={goSubmit} isMobile={isMobile} />
          </Route>
          <Route path={routers.cloudHosting} exact>
            <Cloud goSubmit={goSubmit} isMobile={isMobile} />
          </Route>
          <Route path={routers.webDevelopment} exact>
            <Web isMobile={isMobile} />
          </Route>
          <Route path={routers.softwareService} exact>
            <Software isMobile={isMobile} />
          </Route>
          <Route path={routers.dataCenterService} exact>
            <Data isMobile={isMobile} />
          </Route>
          <Redirect from="*" to={routers.home} />
        </Switch>
        <BackToTop />
        <SubmitRequest message={message} />
        <Footer routers={routers} />
      </div>
  );
};

export default DefaultLayout;
