import React, { useState, useEffect, useCallback } from 'react'
import { useLocation } from 'react-router-dom'
import Select from 'react-select'

import './index.scss'

const SUBJECT_OPTIONS = [
  {
    value: '1',
    label: 'Sales'
  },
  {
    value: '2',
    label: 'Product/Solution enquiry'
  },
  {
    value: '3',
    label: 'Customer service'
  },
  {
    value: '4',
    label: 'Other enquiry'
  }
]

const colourStyles = {
  control: (styles) => ({
    ...styles,
    backgroundColor: 'transparent',
    border: 'none!important',
    boxShadow: 'none!important'
  }),
  option: (styles, { data, isDisabled, isFocused, isSelected }) => {
    return {
      ...styles,
      backgroundColor: null,
      color: 'black',
      cursor: isDisabled ? 'not-allowed' : 'default',
      ':active': {
        ...styles[':active'],
        backgroundColor: 'transparent'
      },
      ':hover': {
        backgroundColor: '#e5e5e5'
      }
    }
  },
  input: (styles) => ({ ...styles }),
  placeholder: (styles) => ({ ...styles }),
  singleValue: (styles, { data }) => ({ ...styles }),
  ':active': {
    outline: 'none'
  }
}

const SubmitRequest: React.FC<{ message?: string }> = ({ message = '' }) => {
  const { pathname } = useLocation()
  const [sendCopy, setSendCopy] = useState(false)

  const toggleSendCopy = useCallback((e) => {
    e.preventDefault()
    setSendCopy((prev) => !prev)
  }, [])

  useEffect(() => {
    document.getElementsByTagName('textarea')[0].value = message
  }, [message])

  return (
    <div
      id="bottom"
      className={['submit-request', pathname.substr(1)].join(' ')}
    >
      <div className="container">
        <h2 className="title">Submit a request</h2>
        <div className="form">
          <div className="form-row">
            <label className="form-label required">Enquiry type</label>
            <div className="form-field">
              <Select
                className="selector"
                placeholder=" Please select enquiry type"
                components={{
                  IndicatorSeparator: null
                }}
                options={SUBJECT_OPTIONS}
                styles={colourStyles}
              />
            </div>
          </div>
          <div className="form-row-half">
            <div className="form-row">
              <label className="form-label required">Name</label>
              <div className="form-field">
                <input type="text" className="form-input" />
              </div>
            </div>
            <div className="form-row">
              <label className="form-label">Company name (optional)</label>
              <div className="form-field">
                <input type="text" className="form-input" />
              </div>
            </div>
          </div>
          <div className="form-row-half">
            <div className="form-row">
              <label className="form-label required">Email</label>
              <div className="form-field">
                <input type="email" className="form-input" />
              </div>
            </div>
            <div className="form-row">
              <label className="form-label">Contact number (optional)</label>
              <div className="form-field">
                <input type="text" className="form-input" />
              </div>
            </div>
          </div>
          <div className="form-row">
            <label className="form-label">Message</label>
            <div className="form-field textarea">
              <textarea className="message" />
            </div>
          </div>
        </div>
        <div className="send-me-copy">
          <input
            type="checkbox"
            className={['checkbox', sendCopy ? 'checked' : ''].join(' ')}
            onClick={toggleSendCopy}
          />
          Send me a copy
        </div>
        <button className="submit">Submit</button>
      </div>
    </div>
  )
}

export default React.memo(SubmitRequest)
